:root{--primaryColor: #E03E0E;--primaryColor2: #FA8717;--primaryColor3: #FEE6CF;--bannerColor: #EA5023;--fontFamily: 'Nunito', sans-serif;--templateColor: #E03E0E;--templateColor2: #FF8A73;--whiteColor: #ffffff;--lightColor: #F8F8FC;--darkColor: #7C7C80;--blackColor: #000000;--paragraphColor: #818992;--transition: .5s;--darkColor1: #474747;--blue: #4A50FF;--blueActive: #7D81F4;--pink: #D74A96;--pinkHover: #F076B8;--lightBlue: #6195FD;--lightBlueHover: #1763FB;--mint: #24B6B1;--mintHover: #2C9491;--green: #47821C;--greenHover: #5BB01C;--purpule: #7B70FF;--purpleHover: #5145E2;--yellow: #FFC736;--yellowHover: #FBB911;--breadcrumbtextcolor: #D1C4E9;--breadcrumbtextcoloractive: #fff}.breadcrumb-wrapper{box-sizing:border-box;padding:3rem 0;display:flex;flex:1;justify-content:space-between;align-items:center}.breadcrumb-wrapper .page__section{color:var(--darkColor);border-radius:2px;display:flex;flex:1;align-items:center}.breadcrumb-wrapper .page__section:nth-of-type(n+2){margin-top:2.5rem}.breadcrumb-wrapper .page__section .breadcrumb{display:flex;flex:1;align-items:center;margin-bottom:0}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list{display:flex;flex-wrap:wrap;gap:.75rem;margin-bottom:0}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group{display:inline-flex;align-items:center}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group a.breadcrumb__point{font-style:normal;font-weight:normal;font-size:20px;line-height:140%;color:#7C7C80}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group .breadcrumb__divider{margin-left:.75rem;font-size:20px;color:#C4C4C4}.breadcrumb-wrapper .page__section .breadcrumb .breadcrumb__list .breadcrumb__group span.breadcrumb__point{font-style:normal;font-weight:bold;font-size:20px;line-height:140%;color:var(--primaryColor)}
